.page-header{
    width:100%;
    min-height:100vh;
    height:fit-content;
    background-image: linear-gradient(black, blue);

    background-size: 100% 100%;
    display:flex;
    flex-direction:row;
    justify-content: center;
    overflow: hidden;
    margin:auto;

}

 


.page-header-wrap{
    display:flex;
    flex-direction:column;
    justify-content: center;
}

.page-header-profile{
    position:absolute;
    padding: 0.4em;
    top:20%;
    box-sizing: border-box;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    line-height: 1.2;
    letter-spacing: 0.03em;
    font-family: 'Raleway', 'Lato', sans-serif;
    font-weight: 400;
    text-align:center;
    color:white;
    margin:auto;
    font-size:40px;  
    
    animation-duration: 3s;
    animation-iteration-count: 1;
    
    display:flex;
    flex-direction:column;
    justify-content:center;

    .page-header-name{
        h1,h2,h3{
            margin:0em;
            text-shadow: 0 0 3px #ffffff;
            transition: all 0.3s ease-in-out;
        }
    }   

    @media only screen and (max-width: 500px) {

        top:15%;
        //font-size:29px;  
        .page-header-name{
            font-size:29px;  
            h1,h2,h3{
                text-shadow: 0 0 2px #ffffff;
            }
        }

        .page-header-title{
            font-size:18px; 
            margin-bottom:0vh; 

        }

        .page-header-button-enter{
            margin-top:2vh;
        }

        .page-header-icons{

            width:80%;

        }

    }

    @media only screen and (max-width: 450px) {
        top:10%;
    }
    
    
}
  
.title{
    margin:0.1em;
    padding:1em;
    font-size:19px;
    text-shadow: 0 0 2px #ffffff;
    
}

  
.page-header-title{
    margin:0.1em;
    padding:1em;
    font-size:19px;
    text-shadow: 0 0 2px #ffffff;
    
}


.page-header-button-enter:hover{
    box-shadow: 0 0 10px #1e7bf4;
}

.page-header-icons{
    
    margin:auto;
    margin-top:2.2vh;
    width:60%;
    display:flex;
    flex-direction:row;
    justify-content:center;
}

.page-header-icon{
    opacity:0.7;
    margin:0.2em;
    transition: all 0.3s ease-in-out;
}

 
 



